


































































































































































import { ApiService } from "@/x-vue/services/api.service";
import { UserModel } from "@/x-vue/services/interfaces";
import Vue from "vue";
import Component from "vue-class-component";
import Service from "../../../services/x-vue.service";

@Component({})
export default class AdminUserList extends Vue {
  s = Service.instance;
  users: Array<UserModel> = [];
  total = 0;

  limit = 5;
  searchKey = "";
  noOfPages = 10;
  currentPage = "1";

  options = {
    email: true,
    firebaseUid: false,
    name: true,
    nickname: true,
    point: true,
    phoneNo: true,
    gender: false,
    birthdate: false,
    countryCode: false,
    province: false,
    city: false,
    address: false,
    zipcode: false,
    createdAt: false,
    updatedAt: false,
  };

  editLink(user: UserModel): string {
    return "/admin/user/edit/" + user.idx + window.location.search;
  }

  linkGen(pageNum: number): string {
    return pageNum === 1 ? "?" : `?page=${pageNum}`;
  }

  onPageChanged(page: number): void {
    this.currentPage = "" + page;
    this.onSubmitSearch();
  }

  mounted(): void {
    // console.log("mounted(): void {::", this.currentPage);
    this.currentPage = this.$route.query.page
      ? (this.$route.query.page as string)
      : "1";
    this.onSubmitSearch();
  }

  async onSubmitSearch(): Promise<void> {
    try {
      this.users = await ApiService.instance.userSearch({
        searchKey: this.searchKey,
        limit: this.limit,
        page: this.currentPage,
        full: true,
      });

      this.total = await ApiService.instance.userCount({
        searchKey: this.searchKey,
      });
      this.noOfPages = Math.ceil(this.total / this.limit);
    } catch (e) {
      this.s.error(e);
    }
  }
}
